<template>
  <div>
    <div class="step-container">
      <el-steps :active="active">
        <el-step title="选择服务对象" />
        <el-step title="编辑随访内容" />
        <el-step title="预览发送" />
      </el-steps>
    </div>
    <div v-if="active === 0">
      <div class="choose-customer">
        <healthFileList
          :showOperator="false"
          :radioColumn="true"
          :serviceItem="'health_follow'"
          @rowClick="setSelectBaby"
        >
        </healthFileList>
        <div class="next-step">
          <el-button type="primary" size="mini" @click="gotoStepTwo"
            >下一步</el-button
          >
        </div>
      </div>
    </div>
    <div v-if="active === 1 || active === 2" class="edit-container">
      <el-row>
        <el-col :span="6">
          <baby-info-area :page-type="'follow'" :baby-id="closeRouterBabyId" />
        </el-col>
        <el-col :span="18">
          <div class="edit-content">
            <el-row class="edit-section">
              <el-col v-show="active === 1" class="edit-form" :span="24">
                <send-content-form
                  ref="contentForm"
                  v-model="ruleForm"
                  @preview="preview"
                  @handleSave="handleSave"
                  @handleClose="handleClose"
                />
              </el-col>
              <el-col v-if="active === 2" :span="23">
                <div class="preview">
                  <send-content-preview
                    :task-id="createTaskId"
                  ></send-content-preview>
                </div>
                <div class="preview-button-container">
                  <el-button @click="active--">返回修改</el-button>
                  <el-button
                    type="primary"
                    @click="sendTask"
                    :loading="sendLoading"
                    >发 送</el-button
                  >
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      :center="true"
      width="22%"
    >
      请选择要发送随访消息的患者后再进行下一步操作
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <task-close-dialog
      @closeTask="closeTask"
      @closeDialog="closeDialogVisible = false"
      :closeDialogVisible="closeDialogVisible"
    ></task-close-dialog>
  </div>
</template>

<script>
import healthFileList from '../healthFile/components/healthFileList'
import SendContentForm from './components/SendContentForm'
import SendContentPreview from './components/SendContentPreview'
import TaskCloseDialog from '@/components/TaskCloseDialog'
import BabyInfoArea from '@/views/medical-service/diagnose/extend'
import {
  getTaskContent,
  updateTaskContent,
  updateSavedTaskContent,
  sendTask,
  closeTask
} from '@/api/obstetricsFollow'
import { getSimpleText } from '@/utils/healthManagement'
import _ from 'lodash'

export default {
  name: 'Send',
  components: {
    BabyInfoArea,
    SendContentForm,
    SendContentPreview,
    TaskCloseDialog,
    healthFileList
  },
  data() {
    return {
      sendLoading: false,
      closeDialogVisible: false,
      isSaved: false,
      createTaskId: '',
      dialogVisible: false,
      ruleForm: {
        title: '',
        remark: '',
        delay_send_reason: '',
        content: '',
        cover: '',
        summary: '',
        videos: []
      },
      checkList: [],
      active: 0,
      currentTask: {},
      selectBaby: null
    }
  },
  computed: {
    closeRouterBabyId() {
      return this.$route.query.babyId
        ? this.$route.query.babyId
        : _.get(this.selectBaby, '_id')
    },
    closeRouterTaskId() {
      return this.$route.query ? this.$route.query.taskId : this.createTaskId
    },
    isReplace() {
      return this.currentTask.is_replace
        ? this.currentTask.is_replace
        : this.selectBaby && this.selectBaby.is_replace
    }
  },
  created() {
    if (this.$route.query.taskId) {
      this.active = 1
      // 获取任务详情
      getTaskContent(this.$route.query.taskId).then(res => {
        this.ruleForm = res.data
        this.$refs.contentForm.fileList = res.data.videos
      })
    }
  },
  methods: {
    setSelectBaby(val) {
      this.selectBaby = val
    },
    // 分页时查询宝宝列表
    searchBabyList() {
      this.$refs.searchConditions.search()
    },

    // 从富文本中提取文本文字
    getSimpleText,
    // 保存直接发送的图文消息
    saveTaskContent() {
      if (this.$route.query.babyId || this.createTaskId) {
        const data = {
          family_member: this.$route.query.babyId
            ? this.$route.query.babyId
            : this.selectBaby._id,
          title: this.ruleForm.title,
          content: this.ruleForm.content,
          cover: this.ruleForm.cover,
          summary: this.getSimpleText(this.ruleForm.content),
          delay_send_reason: this.ruleForm.delay_send_reason,
          videos: this.ruleForm.videos,
          task_type: 'health_follow',
          task_message_type: this.ruleForm.task_message_type
        }
        return updateSavedTaskContent(
          this.$route.query.taskId || this.createTaskId,
          data
        )
      } else {
        const data = {
          family_member: this.selectBaby._id,
          title: this.ruleForm.title,
          content: this.ruleForm.content,
          cover: this.ruleForm.cover,
          summary: this.getSimpleText(this.ruleForm.content),
          videos: this.ruleForm.videos,
          task_type: 'health_follow',
          task_message_type: 'direct_send'
        }
        return updateTaskContent(data)
      }
    },
    // 点击保存按钮
    handleSave() {
      this.saveTaskContent().then(res => {
        this.$message.success('保存成功')
        this.createTaskId = res.data._id
        this.isSaved = true
      })
    },
    // 预览图文消息
    preview() {
      if (!this.isSaved) {
        this.saveTaskContent().then(res => {
          this.createTaskId = res.data._id
          this.active++
        })
      } else {
        this.active++
      }
    },
    // 任务发送
    sendTask() {
      this.sendLoading = true
      sendTask(this.createTaskId)
        .then(res => {
          this.sendLoading = false
          this.$message.success('消息发送成功')
          this.$router.back(-1)
        })
        .catch(err => {
          this.sendLoading = false
        })
    },
    // 点击关闭任务
    handleClose() {
      if (!this.createTaskId && !this.$route.query.taskId) {
        this.$message.warning('任务还未创建')
        return
      }
      this.closeDialogVisible = true
    },
    closeTask(reasonForm) {
      // 请求取消任务的接口,取消成功后刷新任务列表
      const params = {
        closed_reason: reasonForm.closeReasonType,
        closed_reason_message: reasonForm.closeReason
      }
      const closeId = this.$route.query.taskId || this.createTaskId
      closeTask(closeId, params).then(res => {
        const operatorName = '关闭任务'
        this.$message({
          message:
            res && res.data.result === true
              ? operatorName + '成功'
              : operatorName + '失败',
          type: res && res.data.result ? 'success' : 'error'
        })
        this.closeDialogVisible = false
        setTimeout(() => {
          this.$router.push({
            path: 'close',
            query: {
              babyId: this.closeRouterBabyId,
              taskId: this.closeRouterTaskId
            }
          })
        }, 1000)
      })
    },
    gotoStepTwo() {
      if (!this.selectBaby) {
        this.dialogVisible = true
        return
      }
      this.active++
    }
  }
}
</script>
<style lang="scss">
/* 覆盖样式改变表格行高 */
.edit-section {
  .el-table__header tr,
  .el-table__header th {
    padding: 0;
    height: 50px;
  }
  .el-table__body tr,
  .el-table__body td {
    padding: 0;
    height: 50px;
  }
  .el-table--enable-row-hover .el-table__body tr:hover > td {
    background-color: #fff !important;
  }
}
</style>
<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
.next-step {
  position: relative;
  z-index: 99;
}
</style>
