<template>
  <div>
    <div class="preview">
      <div class="preview__title">
        {{ ruleForm.title }}&nbsp;&nbsp;
        <svg-icon v-if="replace" icon-class="replace" />
      </div>
      <div
        class="preview__content rich-text rich-text__image--max380"
        v-html="ruleForm.content"
      />
    </div>
    <div v-if="ruleForm.videos && ruleForm.videos.length > 0">
      <div
        v-for="{ url } in ruleForm.videos"
        :key="url"
        style="text-align: center"
      >
        <video-item :url="url" />
      </div>
    </div>
  </div>
</template>

<script>
import VideoItem from './common/VideoItem'
import { previewTask } from '@/api/obstetricsFollow'

export default {
  name: 'SendContentPreview',
  components: {
    VideoItem
  },
  data() {
    return {
      ruleForm: {
        title: '',
        content: '',
        videos: []
      }
    }
  },
  props: {
    taskId: {
      type: String,
      default: ''
    },
    replace: {
      type: Boolean,
      default: false
    }
  },
  created() {
    previewTask(this.taskId).then(res => {
      this.ruleForm = res.data
    })
  }
}
</script>

<style scoped lang="scss">
.preview {
  padding: 10px 60px;
  line-height: 22px;
  font-size: 16px;
  &__title {
    margin-top: 30px;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 20px;
  }
}
</style>
